.addprod{
    background-image:url("./../public/images/back2.jpg") ;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    
}
.pod-dets{
    border-radius: 10px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    padding: 40px;
    background-color: #273263;
    margin-top: 10vh;
    /* align-self: center; */
    width: max-content;
    height: max-content;
    gap: 10px;
    display: flex;
    flex-direction: column;
    /* align-items:center ; */
}
.pod-dets>span{
    color:rgb(142, 236, 142);
    text-decoration: underline;
    font-size: 30px;
}
.pod-dets>input{
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    background-color: #fffbfb;
    border: none;
    border-radius: 10px;
    align-self: center;
    width: 220px;
    height: 25px;
    padding: 5px;
}
.pod-dets>input:focus{
    outline: none;
}
.pod-dets>select{
    align-self: center;
    width: 100px;
    /* align-self: flex-start; */
    
}
.pod-dets>button{
    align-self: center;
    background-color: #f0af7d;
    width: 200px;
    height: 40px;
    padding: 5px;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    transition: background-color 0.5s ease-in-out;

}
.pod-dets>button:hover{
    /* transition: all 1s ease-in-out; */
    background-color:rgb(192, 111, 207);
    color: #ffff;
}